@import url(https://fonts.googleapis.com/css2?family=Pacifico&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Lora:ital,wght@1,700&family=Open+Sans:wght@300&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Roboto:wght@100&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

html {
  overflow: scroll;
  overflow-x: hidden;
}

a {
  color: inherit;
  text-decoration: inherit;
}

::-webkit-scrollbar {
  background: #1E2024;
}

::-webkit-scrollbar-thumb {
  background: #151617;
}

.App {
  text-align: center;
  overflow: hidden;
}

.App * {
  transition: all .3s ease;
}

.App-header {
  background-color: #24272c;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.mainContainer {
  padding: 0px 120px;
}

img {
  display: block;
}

.setsTable {
  display: flex;
  flex-direction: row;
  justify-content: center;
  flex-wrap: wrap;
}

.setContainer {
  margin: 30px;
  margin-top: 45px;
}

.setContainer * {
  border-radius: 25px;
  height: 300px;
}

.rows3 * {
  height: 406px;
}
.setLabel {
  margin-bottom: 15px;
  font-family: 'Pacifico', cursive;
  height: auto;
}
.mainHeader {
  margin: 60px 0 0px 0;
  font-family: 'Lora', serif;
  font-size: 3.4em;
}

h1 {
  margin: 60px 0 0px 0;
  font-family: 'Lora', serif;
}

.commentary {
  margin: 40px 0 20px 0;
  font-size: 90%;
  font-family: 'Lora', serif;
}

s {
  font-size: 60%;
}

.description {
  text-indent: 1.5em;
  width: 48em;
  text-align: justify;
  margin: 20px auto 10px auto;
  font-size: 70%;
  font-family: 'Lora', serif;
}

.imageContainer {
  position: relative;
}

.setOverlay {
  position: absolute;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  height: 100%;
  width: 0;
  opacity: 0.7;
  background-color: rgb(40, 40, 44);
  display: block;
  height: auto;
  overflow: hidden;
  border-radius: 25px 0 0 25px;
}

.setOverlay * {
  transform: translate(-120%);
}

.imageContainer:hover .setPreview {
  -webkit-filter: brightness(0.5);
          filter: brightness(0.5)
}

.imageContainer:hover .setOverlay * {
  transform: translate(0px);
}

.imageContainer:hover .setOverlay {
  width: 150px;
}

.imageContainer:hover .text {
  opacity: 0.9;
}

.imageContainer:hover .downloadIcon {
  opacity: 0.9;
}

.text {
  white-space: nowrap;
  opacity: 0;
  color: white;
  font-size: 26px;
  overflow: hidden;
}

.setOverlayButton {
  margin: 20px auto 0 auto;
  cursor: pointer;
  text-align: center;
  display: flex;
  justify-content: center;
  align-items: center;
  width: 60%;
  height: 45px;
  border: rgb(116, 115, 122) solid 3px;
  border-radius: 10px;
  background-color: rgba(65, 64, 68, 0.7);
}

.setOverlayButton:hover {
  border: rgb(167, 165, 180) solid 3px;
  background-color: rgba(91, 89, 95, 0.7);
  transition: .2s ease;
}

.downloadIcon {
  margin: 0;
  opacity: 0;
  margin-left: auto;
  margin-right: auto;
  border-radius: 0;
  width: 45px;
  height: 45px;
  -webkit-filter: invert(76%) sepia(7%) saturate(130%) hue-rotate(131deg) brightness(93%) contrast(93%);
          filter: invert(76%) sepia(7%) saturate(130%) hue-rotate(131deg) brightness(93%) contrast(93%);
}

.downloadContainer {
  padding: 25px 0 0 0;
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
}
.timerText {
  font-family: 'Roboto', sans-serif;
  font-size: 194px;
}

.fontButton {
  margin: 20px auto 0 auto;
  padding: 15px 20px;
  cursor: pointer;
  text-align: center;
  justify-content: center;
  align-items: center;
  display: inline-block;
  width: auto;
  height: 45px;
  border: rgb(116, 115, 122) solid 3px;
  border-radius: 10px;
  background-color: rgba(65, 64, 68, 0.7);
}

.fontButton:hover {
  border: rgb(167, 165, 180) solid 3px;
  background-color: rgba(91, 89, 95, 0.7);
  transition: .2s ease;
}

@font-face {
  font-family: 'Impact';
  src: url(/HG2020/static/media/Impact.848010fa.woff) format('woff');
}

@font-face {
  font-family: 'ljk_RoadRage';
  src: url(/HG2020/static/media/ljk_RoadRage.ee2aaa1d.woff) format('woff');
}

.Impact {
  font-family: Impact, Haettenschweiler, 'Arial Narrow Bold', sans-serif;
}

.RoadRage {
  font-family: 'ljk_RoadRage', Fallback, sans-serif;
}

.yellow {
  color: #d6e030;
}

.decorations * {
  position: absolute;
  width: 13%;
}

.UL {
  left: -1.5%;
  top: -3%;
}

.EGG {
  left: calc(1920px*3.0 - 200px*3.0);
  top: 0px;
  height: 125%;
  width: auto;
}

.EGG1 {
  left: calc(1920px*2.0 - 550px*2.0);
  top: 0px;
  height: 125%;
  width: auto;
}
.foot {
  font-size: 40%;
  color: #302829;
}

@media only screen and (max-width: 600px) {
  * {
    width: 100%;
    min-height: 0;
    height: auto!important;
  }
  .mainHeader {
    margin: 6px 0 0px 0;
    font-size: 1.8em;
  }
  h1 {
    margin: 60px 0 0px 0;
    font-size: 170%;
  }
  .commentary {
    margin: 30px 0 20px 0;
    font-size: 115%;
  }
  s {
    font-size: 60%;
  }
  .description {
    text-indent: 1em;
    width: 90%;
    font-size: 85%;
  }
  .timerText {
    font-size: 394%;
  }
  .imageContainer:hover .setOverlay {
    width: 115px;
  }
  .downloadIcon {
    width: 30px;
    height: 30px;
  }
  .foot {
    visibility:hidden;
  }
}
